.dropdown__title {
    background-color: var(--primary-color);
    color: white;
    font-size: 24px;
    font-weight: 500;
    cursor: pointer;
    height: 50px;
    margin-top: 30px;
    margin-bottom: 0px;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    align-items: center;
    border-radius: 5px;
}

.dropdown__text {
    background-color: var(--secondary-color);
    font-size: 24px;
    font-weight: 400;
    padding: 30px 20px;
    line-height: 1.25;
    border-radius: 5px;
    opacity: 1;
}

.dropdown__text.close{
    opacity: 0;
    height: 0;
    padding: 0;
    visibility: hidden;
}

.dropdown__icon {
    display: flex;
}

.dropdown__icon.open{
    transform: rotate(180deg);
}

@media screen and (max-width : 740px) {
    .dropdown__title{
        font-size: 13px !important;
        padding: 0 10px;
        margin-top: 20px;
        height: 30px;
    }

    .dropdown__text {
        font-size: 12px !important;
    }

    .dropdown__icon {
        height: 15px;
    }

    .dropdown__text {
        padding: 15px 10px;
    }
 }